<template>
    <div class="borrow-order-list">
	  	<div class="container">
		  	<!-- S 统计 -->
		    <div class="statistics">
		    	<div class="item">
		    		总订单数
		    		<span>199</span>
		    	</div>
		    	<div class="item">
		    		总销售额
		    		<span>199</span>
		    	</div>
		    	<div class="item">
		    		逾期总收益
		    		<span>199</span>
		    	</div>
		    </div>
		    <!-- E 统计 -->

		    <!-- S 搜索 -->
		    <div class="search">
		     	<cube-input
				:placeholder="placeholder"
				 :clearable="clearable"
				 v-model="keywords"
		     	></cube-input>
		     	<cube-button type="submit" @click="doSearch">搜索</cube-button>
		    </div>
		    <!-- E 搜索 -->

			<!-- S 订单列表 -->
		    <div class="order">
				<cube-scroll
				  ref="scroll"
				  direction="horizontal"
				  class="horizontal-scroll-list-wrap">
				  <ul class="list-wrapper">
				    <li class="list-item" :class="selectedLabel === item.label ? 'active' : ''" v-for="(item, index) in tabs" :key="index" @click.stop="selectCate(item.label, index)">{{item.label}}</li>
				  </ul>
				</cube-scroll>

				<div class="order-list">
					<div tag="div" class="order-item" v-for="(order, index) in tabs[selectedIndex].orders" :key="index">
						<router-link :to="'/order/borrow_detail/'+order.id">
							<div class="nickname">
								<h3>{{order.nickname}}</h3>
								<span class="order-state">{{order.order_state}}</span>
							</div>

							<div class="book-info">
								<img :src="order.books[0].thumb" />
								<div class="book-name">
									{{order.books[0].name}}
									<span>市场价：{{order.books[0].price}}</span>
								</div>
								<span class="num">x{{order.num}}</span>
							</div>

							<div class="package">套餐计划：{{order.package}}</div>
							
							<div class="order-type">
								<span>{{order.order_type}}</span>
								<p>共{{order.num}}本图书 合计：￥37.00（包邮）</p>
							</div>
						</router-link>

						<div class="order-operate">
							<div v-if="order.order_state === '待付款'">
								<cube-button>添加图书</cube-button>
								<cube-button @click="cancelOrder(index)">取消订单</cube-button>
							</div>
							<div v-else-if="order.order_state === '交易关闭'">
								<cube-button @click="deleteOrder(index)">删除订单</cube-button>
							</div>
							<div v-else-if="order.order_state === '已归还'">
								<cube-button>发货物流</cube-button>
								<cube-button>还书物流</cube-button>
								<cube-button>确定还书</cube-button>
							</div>
							<div v-else-if="order.order_state === '待发货'">
								<cube-button>添加图书</cube-button>
								<cube-button>立即发货</cube-button>
							</div>
							<div v-else-if="order.order_state === '待回收'">
								<cube-button>延长回收</cube-button>
							</div>
							<div v-else-if="order.order_state === '待评价'">
								<cube-button>立即评价</cube-button>
							</div>
							<div v-else-if="order.order_state === '待签收'">
								<cube-button>发货物流</cube-button>
								<cube-button>确认签收</cube-button>
							</div>
							<div v-else-if="order.order_state === '已逾期'">
								<cube-button @click="remind">提醒还书</cube-button>
							</div>
						</div>
					</div>
				</div>
		    </div>
	   	    <!-- E 订单列表 -->
	    </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
    	placeholder: '请输入订单号/姓名/手机号',
    	clearable: {
	        visible: true,
	        blurHidden: true
	    },
	    selectedLabel: '全部',
	    selectedIndex: 0,
	    tabs: [{
	        label: '全部',
	        orders: [{
		    	id: '1000',
		    	books: [{
		    		name: '我爸爸',
			    	thumb: 'https://imagenew.qujie8.com//uploads/20170624134410-594dfc2a97ba3.jpg',
			    	price: '40.00'
		    	}],
		    	num: 1,
		    	nickname: '昵称',
		    	package: '15天xx本 往返包邮',
		    	order_type: '平台分配订单',
		    	order_state: '待付款'
		    }, {
		    	id: '1001',
		    	books: [{
		    		name: '我妈妈',
			    	thumb: 'https://imagenew.qujie8.com//uploads/20170624134410-594dfc2a97ba3.jpg',
			    	price: '40.00'
		    	}],
		    	num: 1,
		    	nickname: '昵称',
		    	package: '15天xx本 往返包邮',
		    	order_type: '平台分配订单',
		    	order_state: '交易关闭'
		    }, {
		    	id: '1002',
		    	books: [{
		    		name: '小猪佩琪',
			    	thumb: 'https://imagenew.qujie8.com//uploads/20170624134410-594dfc2a97ba3.jpg',
			    	price: '1.00'
		    	}],
		    	num: 1,
		    	nickname: '昵称',
		    	package: '30天xx本 往返包邮',
		    	order_type: '自营订单',
		    	order_state: '已归还'
		    }, {
		    	id: '1003',
		    	books: [{
		    		name: '小猪佩琪',
			    	thumb: 'https://imagenew.qujie8.com//uploads/20170624134410-594dfc2a97ba3.jpg',
			    	price: '1.00'
		    	}],
		    	num: 1,
		    	nickname: '昵称',
		    	package: '30天xx本 往返包邮',
		    	order_type: '平台分配订单',
		    	order_state: '待发货'
		    }, {
		    	id: '1004',
		    	books: [{
		    		name: '小猪佩琪',
			    	thumb: 'https://imagenew.qujie8.com//uploads/20170624134410-594dfc2a97ba3.jpg',
			    	price: '1.00'
		    	}],
		    	num: 1,
		    	nickname: '昵称',
		    	package: '30天xx本 往返包邮',
		    	order_type: '自营订单',
		    	order_state: '待回收'
		    }, {
		    	id: '1005',
		    	books: [{
		    		name: '小猪佩琪',
			    	thumb: 'https://imagenew.qujie8.com//uploads/20170624134410-594dfc2a97ba3.jpg',
			    	price: '35.00'
		    	}],
		    	num: 1,
		    	nickname: '昵称',
		    	package: '30天xx本 往返包邮',
		    	order_type: '平台分配订单',
		    	order_state: '待评价'
		    }, {
		    	id: '1006',
		    	books: [{
		    		name: '小猪佩琪',
			    	thumb: 'https://imagenew.qujie8.com//uploads/20170624134410-594dfc2a97ba3.jpg',
			    	price: '16.00'
		    	}],
		    	num: 1,
		    	nickname: '昵称',
		    	package: '30天xx本 往返包邮',
		    	order_type: '平台分配订单',
		    	order_state: '待签收'
		    }, {
		    	id: '1007',
		    	books: [{
		    		name: '小猪佩琪',
			    	thumb: 'https://imagenew.qujie8.com//uploads/20170624134410-594dfc2a97ba3.jpg',
			    	price: '10.00'
		    	}],
		    	num: 1,
		    	nickname: '昵称',
		    	package: '30天xx本 往返包邮',
		    	order_type: '自营订单',
		    	order_state: '已逾期'
		    }]
	    }, {
	    	label: '待付款'
	    }, {
	    	label: '待发货'
	    }, {
	    	label: '待签收'
	    }, {
	    	label: '待回收'
	    }, {
	    	label: '逾期'
	    }, {
	    	label: '已还书'
	    }, {
	    	label: '待评价'
	    }],
	    keywords: ''
    }
  },
  props: {
  },
  methods: {
  	selectCate(label) {
  		this.selectedLabel = label
  	},
  	doSearch() {
  		if (!this.keywords) {
  			const toast = this.$createToast({
  			type: 'text',
	        txt: '请输入查询关键词',
	        time: 1000,
	        $events: {
	          timeout: () => {
	            console.log('Timeout')
	          }
	        }
	      })
	      toast.show()
	      return false
  		}
  		const toast = this.$createToast({
        txt: '正在查找',
        time: 1000,
        $events: {
          timeout: () => {
            console.log('Timeout')
          }
        }
      })
      toast.show()
  	},
  	deleteOrder(i) {
  		this.$createDialog({
	        type: 'confirm',
	        icon: 'cubeic-close',
	        title: '确定删除订单？',
	        content: '订单删除之后不可恢复',
	        confirmBtn: {
	          text: '确认删除',
	          active: true,
	          disabled: false,
	          href: 'javascript:;'
	        },
	        cancelBtn: {
	          text: '取消',
	          active: false,
	          disabled: false,
	          href: 'javascript:;'
	        },
	        onConfirm: () => {
	          const orderData = this.tabs[0].orders
	          orderData.splice(i,1)
	          this.tabs[0].orders = orderData
	          this.$createToast({
	            type: 'warn',
	            time: 1000,
	            txt: '删除成功'
	          }).show()
	        }
	    }).show()
  	},
  	cancelOrder(i) {
  		this.$createDialog({
	        type: 'confirm',
	        icon: 'cubeic-close',
	        title: '确定取消订单？',
	        content: '',
	        confirmBtn: {
	          text: '确认取消',
	          active: true,
	          disabled: false,
	          href: 'javascript:;'
	        },
	        cancelBtn: {
	          text: '放弃',
	          active: false,
	          disabled: false,
	          href: 'javascript:;'
	        },
	        onConfirm: () => {
	          this.tabs[0].orders[i].order_state = '交易关闭'
	          this.$createToast({
	            type: 'warn',
	            time: 1000,
	            txt: '取消成功'
	          }).show()
	        }
	    }).show()
  	},
  	remind() {
  		this.$createDialog({
	        type: 'alert',
	        icon: 'cubeic-alert',
	        title: '提醒成功',
	        content: '用户将接收到平台推送的逾期信息'
	    }).show()
  	}
  }
}
</script>

<style lang="scss">
@import '~SCSS/_common';
.borrow-order-list{
	padding-top: 0.266667rem;
	.statistics{
		display: flex;
	    align-items: center;
	    justify-content: center;
	    font-size: 0.4rem;
	    background-color:#fff;
	    .item{
	    	flex: 1;
	    	display: flex;
	    	flex-direction:column;
	    	justify-content:center;
	    	align-items:center;
	    	padding:0.4rem 0;
	    	span{
	    		margin-top:0.266667rem;
	    	}
	    }
	}

	.search{
		display: flex;
		background-color:#fff;
		align-items: center;
		padding: 10px 20px 10px 10px;
		margin-top: 20px;
		.cube-input, button{
			height: 0.8rem;
		}
		.cube-input{
			flex:1;
			&_active{
				border: 0;
				outline:none;
			}
			&::after{
				border: 0;
			}
		}
		button{
			width: 1.333333rem;
			padding:0;
			background-color: $mainColor;
		}
	}

	.order{
		.cube-scroll-wrapper{
			background-color:#fff;
		}
		.horizontal-scroll-list-wrap{
		    border-radius: 5px;
		    font-size:0;
		    margin-bottom: 5px;
    		margin-top: 20px;
		    .cube-scroll-content{
		        display: inline-block;
			}
		    .list-wrapper{
		        padding: 0 10px;
			    line-height: 40px;
			    height: 40px;
			    white-space: nowrap;
			    background-color: #fff;
		    }
		    .list-item{
		        display: inline-block;
		        font-size: 0.373333rem;
		        padding: 0.026667rem 0.066667rem;

		        &.active{
		        	color: $mainColor;
		        	font-size:0.426667rem;
		        }
		    }
		}

		.order-list{
			.order-item{
				padding: 0.266667rem;
    			background-color: #fff;
    			margin-bottom: 0.266667rem;
    			&:last-child{
    				margin-bottom: 0;
    			}
				.nickname{
					display: flex;
				    font-size: 0.373333rem;
				    justify-content: space-between;
				    margin-bottom: 0.266667rem;
				    .order-state{
				    	color: $mainColor;
				    }
				}
				.book-info{
					display:flex;
					align-items: flex-start;
					img{
						width: 1.2rem;
						height:1.6rem;
						margin-right:0.266667rem;		
					}
					.book-name{
						flex: 1;
						display:flex;
						flex-direction:column;
						font-size:0.373333rem;
						margin-top: 0.053333rem;
						span{
							margin-top: 0.066667rem;
							color:#9c9c9c;
						}
					}
					.num{
						font-size: 0.373333rem;
					}
				}
				.package,.order-type{
					font-size: 0.373333rem;
					padding: 0.266667rem 0;
				}
				.package{
					border-bottom: 1px solid #f7f7f7;
				}
				.order-type{
					display: flex;
				    font-size: 12px;
				    justify-content: space-between;
				}
				.order-operate{
				    padding: 10px 0;
					border-top: 1px solid #f7f7f7;
					div{
						display:flex;
						justify-content:flex-end;
					}
					button{
						height: 20px;
					    padding: 0;
					    width: 80px;
					    font-size: 14px;
					    background-color: #5DBAA7;
					    margin-left:0.266667rem;
					    border-radius: 0.133333rem;
					}
				}
			}
		}
	}
}
</style>